<template>
	<div v-if="this.cateList.length !== 0">
		<!--  -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/index' }"></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>
		<!--   -->
		<!--  -->
		<el-card class="box-card">
			<el-row>
				<el-col>
					<el-button type="primary" @click="showAddDialogVisible"></el-button>
				</el-col>
			</el-row>
			<!--    vue-table-with-tree-->
			<tree-table class="tree-table" :data="cateList" :columns="columns" :selection-type="false" show-index index-text=""
			 :expand-type="false" border>
				<!--  -->
				<template slot="isok" slot-scope="scope">
					<i class="el-icon-success" v-if="scope.row.cat_deleted === false" style="color: #69c83c;"></i>
					<i class="el-icon-error" v-else style="color: red;"></i>
				</template>
				<!--  -->
				<template slot="order" slot-scope="scope">
					<el-tag size="mini" v-if="scope.row.cat_level === 0"></el-tag>
					<el-tag type="success" size="mini" v-else-if="scope.row.cat_level === 1"></el-tag>
					<el-tag type="warning" size="mini" v-else></el-tag>
				</template>
				<!--  -->
				<template slot="options" slot-scope="scope">
					<el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
					<el-button type="danger" icon="el-icon-delete" size="mini"></el-button>
				</template>
			</tree-table>
			<!--  -->
			<div class="block">
				<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum"
				 :page-sizes="[3,5,7,10]" :page-size="queryInfo.pagesize" layout="total, sizes,prev, pager, next, jumper" :total="total">
				</el-pagination>
			</div>
		</el-card>
		<!--  -->

		<!--  -->
		<el-dialog title="" :visible.sync="dialogVisible" width="50%" @close="resetDialog">
			<!--  -->
			<el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-width="100px">
				<el-form-item label="" prop="cat_name">
					<el-input v-model="addCateForm.cat_name"></el-input>
				</el-form-item>
				<el-form-item label="">
					<!--  -->
					<!-- options  -->
					<!-- props  -->
					<el-cascader expand-trigger="hover" :options="parentCategories" :props="cascaderProps" v-model="selectedKeys"
					 @change="parentCateChanged" clearable change-on-select="true"></el-cascader>
				</el-form-item>
			</el-form>
			<span slot="footer" class="dialog-footer">
				<el-button @click="dialogVisible = false"> </el-button>
				<el-button type="primary" @click="addCategories"> </el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				// 
				cateList: [],
				// 
				queryInfo: {
					type: 3,
					pagenum: 1,
					pagesize: 5
				},
				total: 0,
				columns: [{
						label: '',
						prop: 'cat_name'
					},
					{
						label: '',
						// 
						type: 'template',
						// 
						template: 'isok'
					},
					{
						label: '',
						// 
						type: 'template',
						// 
						template: 'order'
					},
					{
						label: '',
						// 
						type: 'template',
						// 
						template: 'options'
					},
				],
				dialogVisible: false,
				addCateForm: {
					// 
					cat_name: '',
					cat_pid: 0,
					cat_level: 0
				},
				// 
				addCateFormRules: {
					cat_name: [{
							required: true,
							message: '',
							trigger: 'blur'
						},
						{
							min: 3,
							max: 10,
							message: ' 3  10 ',
							trigger: 'blur'
						}
					],
				},
				// 
				parentCategories: [],
				// 
				cascaderProps: {
					value: 'cat_id',
					label: 'cat_name',
					children: 'children'
				},
				// id
				selectedKeys: []
			}
		},
		created() {
			this.getCateList()
		},
		methods: {
			// 
			async getCateList() {
				const {
					data: res
				} = await this.$http.get("categories", {
					params: this.queryInfo
				})
				if (res.meta.status !== 200) {
					return this.$message.error("")
				}
				// cateList
				this.cateList = res.data.result
				this.total = res.data.total
				// console.log(this.cateList)
			},
			handleSizeChange(val) {
				// console.log(val)
				this.queryInfo.pagesize = val
				this.getCateList()
			},
			handleCurrentChange(val) {
				this.queryInfo.pagenum = val
				this.getCateList()
				// console.log(val)
			},
			// 
			showAddDialogVisible() {
				this.getParentCategories()
				this.dialogVisible = true
			},
			// 
			async getParentCategories() {
				const {
					data: res
				} = await this.$http.get("categories", {
					params: {
						type: 2
					}
				})
				if (res.meta.status !== 200) {
					return this.$message.error("")
				}
				this.parentCategories = res.data
				// console.log(res.data)
			},
			// 
			parentCateChanged() {
				// console.log(this.selectedKeys)
				// selectedKeys length  0 
				// 
				if (this.selectedKeys.length > 0) {
					// id
					this.addCateForm.cat_pid = this.selectedKeys[this.selectedKeys.length - 1]
					// 
					this.addCateForm.cat_level = this.selectedKeys.length
				} else {
					this.addCateForm.cat_pid = 0
					this.addCateForm.cat_level = 0
				}


			},
			// 
			resetDialog() {
				this.$refs.addCateFormRef.resetFields()
				this.selectedKeys = []
				this.addCateForm.cat_pid = 0
				this.addCateForm.cat_level = 0
			},
			// 
			addCategories() {
				this.$refs.addCateFormRef.validate(async v => {
					if (!v) return
					const {
						data: res
					} = await this.$http.post("categories", this.addCateForm)
					if (res.meta.status !== 201) {
						return this.$message.error("")
					}
					this.$message.success("")
				})
				this.getCateList()
				// console.log(res)
				this.dialogVisible = false
			}
		}
	}
</script>

<style scoped>
	.tree-table {
		margin-top: 10px;
	}

	.el-cascader {
		width: 100%;
	}
</style>
