<template>
	<div>
		<!--  -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/index' }"></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>
		<!--   -->
		<!--  -->
		<el-card class="box-card">
			<!--  -->
			<el-table :data="limitsInfo" border stripe>
				<el-table-column type="index" label="#"></el-table-column>
				<el-table-column prop="authName" label=""></el-table-column>
				<el-table-column prop="path" label=""></el-table-column>
				<el-table-column prop="level" label="">
					<template slot-scope="scope">
						<el-tag :type="scope.row.color">{{scope.row.levelText}}</el-tag>
					</template>
				</el-table-column>
			</el-table>
			<!-- <el-table-column prop="authName" label=""></el-table-column> --> 
		</el-card>
		<!--  --> 
	</div>
</template>
<script>
	export default{
		data(){
			return{
				limitsInfo:[],
				type:'list',
				color:'',
				levelText:''
			}
		},
		created() {
			this.getLimitsInfo()
		},
		methods:{
			// 
			levelTag(){
				this.limitsInfo.forEach(v=>{
					if(v.level == 0){
						this.color = 'primary'
						this.levelText = ''
					}else if(v.level == 1){
						this.color = 'success'
						this.levelText = ''
					}else{
						this.color = 'warning'
						this.levelText = ''
					}
					v.color = this.color
					v.levelText = this.levelText
				})
				// console.log(this.color)
			},
			async getLimitsInfo(){
				const {data:res} = await this.$http.get(`rights/${this.type}`)
				if(res.meta.status !==200){
					return this.$message.error("")
				}
				this.limitsInfo = res.data
				// console.log(this.limitsInfo)
				this.levelTag()
			}
		}
	}
</script>
