<template>
	<div class="container">
		<div class="title">
			<div class="title-item">
				<div class="title-icon">
					<img style="width: 100%; height: 100%; border-radius: 50%;" src="../assets/2.jpg" />
				</div>
				<span></span>
			</div>
			<el-row>
				<!--  -->
				<el-button :style="selfstyle" type="info" @click="loginOut"></el-button>
			</el-row>
		</div>
		<el-container>
			<el-container>
				<!--  -->
				<el-aside :width="isCollapse ? '64px':'200px'">
					<!--  -->
					<div class="toggle-button" @click="toggleCollapse">|||</div>
					<!--  -->
					<el-menu background-color="#2d313c" text-color="#fff" active-text-color="#409eff" unique-opened :collapse="isCollapse" router
					:collapse-transition="false" :default-active="activePath"
					>
						<!--  -->
						<!-- indexIndex -->
						<!-- item.idindex -->
						<el-submenu :index="item.id +''" v-for="item in menuList" :key = "item.id">
							<!--  -->
							<template slot="title">
								<!--  -->
								<!-- id -->
								<i :class="iconsObj[item.id]"></i>
								<!--  -->
								<span>{{item.authName}}</span>
							</template>
							<!--  -->
							<el-menu-item :index="'/'+ items.path" v-for="items in item.children" :key = "items.id" @click="getStatus('/'+ items.path)">
								<i class="el-icon-menu"></i>
								<span>{{items.authName}}</span>
							</el-menu-item>
						</el-submenu>
					</el-menu>
				</el-aside>
				<el-main>
					<router-view></router-view>
				</el-main>
			</el-container>
		</el-container>
	</div>
</template>

<script>
	import '../assets/font/iconfont.css'
	export default {
		data() {
			return {
				isCollapse:false,
				selfstyle: {
					color: "white",
					marginRight: "20px",
					width: "100px",
					height: "50px",
					backgroundColor: "#909399"
				},
				menuList:[],
				//
				iconsObj:{
					'125':'iconfont icon-user',
					'103':'iconfont icon-tijikongjian',
					'101':'iconfont icon-shangpingouwudai2',
					'102':'iconfont icon-danju-tianchong',
					'145':'iconfont icon-baobiao'
				},
				//
				activePath:'',

			}
		},
		created(){
			this.getMenuList()
			this.activePath = window.sessionStorage.getItem('activePath')
		},
		methods: {
			//
			loginOut() {
				//token
				window.sessionStorage.clear()
				//
				this.$router.push("/")
			},
			async getMenuList(){
				const {data:res} = await this.$http.get('menus')
				// console.log(res)
				//res.meta.status200message
				if(res.meta.status != 200) return this.$message.err(res.meta.msg)
				this.menuList = res.data
				// this.menuList.forEach(v=>{
				// 	console.log(v.id)
				// }) 
				// console.log(this.menuList)
			},
			//element-uicollapse
			toggleCollapse(){
				this.isCollapse = !this.isCollapse
			},
			getStatus(activePath){
				//activeParh
				window.sessionStorage.setItem('activePath',activePath)
				this.activePath = activePath
				console.log(activePath) 
			}
		}
	}
</script>
<style>
	html,
	body {
		overflow: hidden;
		height: 100%;
	}
</style>
<style >
	.iconfont{
		margin-right: 10px;
	}
	.el-submenu .el-menu-item {
		color: white;
	}

	.el-menu-item-group {
		background-color: #2d313c;
	}

	.el-submenu {
		background-color: #2d313c;
	}

	.container {
		width: 100%;
		height: 100%;
		background-color: #2D313C;
	}

	.title {
		display: flex;
		width: 100%;
		height: 90px;
		color: #FFFFFF;
		font-size: 25px;
		align-items: center;
		justify-content: space-between;
	}

	.title-item {
		display: flex;
		align-items: center;
	}

	.title-icon {
		width: 50px;
		height: 50px;
		background-color: white;
		border-radius: 50%;
		margin: 0 15px 0 10px;
	}

	.selfbutton {
		width: 100px;
		height: 50px;
		font-size: 50px;
		font-weight: 600;
		margin-right: 30px;
	}

	.el-container {
		height: 100%;
	}

	.el-aside {
		height: 100%;
		
	}
	/*  */
	.el-aside .el-menu{
		border-right: none;
	}
	.el-main {
		background-color: white;
		height: 100%;
	}

	.template {
		color: white;
	}
	.toggle-button{
		background-color: #5a6278;
		text-align: center;
		font-size: 10px;
		color: white;
		line-height: 24px;
		letter-spacing: 0.2em;
		cursor: pointer;
	}
</style>
