<template>
	<div>
		<!--  -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/index' }"></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>
		<!--   -->
		<!--  -->
		<el-card class="box-card">
			<el-button type="primary"></el-button>
			<!--  -->
			<el-table :data="roleInfoList" border stripe>
				<el-table-column type="expand">
					<template slot-scope="scope">
						<!-- 1. 2.1 -->
						<el-row :class="['bdbottom', index1 === 0?'bdtop':'']" v-for="(item1,index1) in scope.row.children" :key="item1.id">
							<!--  -->
							<el-col :span="5">
								<el-tag closable @close="removeRoleListById(scope.row.id,item1.id)"> {{item1.authName}}</el-tag>
								<i class="el-icon-caret-right"></i>
							</el-col>
							<!--  -->
							<el-col :span="19">
								<!--  -->
								<!-- 00 -->
								<el-row :class="index2 === 0?'':'bdtop'" v-for="(item2,index2) in item1.children" :key="item2.id">
									<el-col :span="6">
										<el-tag @close="removeRoleListById(scope.row.id,item2.id)" closable type="success"> {{item2.authName}}</el-tag>
										<i class="el-icon-caret-right"></i>
									</el-col>
									<el-col :span="18">
										<el-tag @close="removeRoleListById(scope.row.id,item3.id)" type="warning" v-for="(item3) in item2.children"
										 :key="item3.id" closable> {{item3.authName}}</el-tag>
									</el-col>
								</el-row>
							</el-col>
						</el-row>
					</template>
				</el-table-column>
				<el-table-column type="index" label="#"></el-table-column>
				<el-table-column prop="roleName" label=""></el-table-column>
				<el-table-column prop="roleDesc" label=""></el-table-column>
				<el-table-column label="">
					<template slot-scope="scope">
						<el-button type="primary" icon="el-icon-edit" size="small"></el-button>
						<el-button type="danger" icon="el-icon-delete" size="small"></el-button>
						<el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
							<el-button type="warning" icon="el-icon-setting" size="small" @click="setRightDialogVisible(scope.row)"></el-button>
						</el-tooltip>
					</template>
				</el-table-column>
			</el-table>
		</el-card>
		<!--  -->
		<!--  -->
		<el-dialog title="" :visible.sync="dialogVisible" width="50%" @close="setRightDialogClosed">
			<!-- element-ui Tree  -->
			<el-tree :data="limitsInfo" :props="treeProps" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys" ref="treeRef">
			</el-tree>
			<span slot="footer" class="dialog-footer">
				<el-button @click="dialogVisible = false"> </el-button>
				<el-button type="primary" @click="allotRights"> </el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				roleInfoList: [],
				dialogVisible: false,
				type: 'tree',
				limitsInfo: [], 
				// 
				treeProps: {
					children: 'children', 
					label: 'authName'
				},
				// id
				defKeys:[],
				roleId:''
			}
		},
		created() {
			this.getroleInfoList()
		},
		methods: {
			// 
			async getroleInfoList() {
				const {
					data: res
				} = await this.$http.get("roles")
				if (res.meta.status !== 200) {
					return this.$message.error("")
				}
				this.roleInfoList = res.data
				// console.log(this.roleInfoList)
			},
			//
			removeRoleListById(roleId, rightId) {
				//
				this.$confirm(', ?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(async () => {
					const {
						data: res
					} = await this.$http.delete(`roles/${roleId}/rights/${rightId}`)
					this.getroleInfoList()
					this.$message({
						type: 'success',
						message: '!'
					});
				}).catch(() => {
					this.$message({
						type: 'info',
						message: ''
					});
				});
			},
			// ,
			async setRightDialogVisible(role) {
				const {
					data: res
				} = await this.$http.get(`rights/${this.type}`)
				if (res.meta.status !== 200) {
					return this.$message.error("")
				} 
				this.limitsInfo = res.data
				this.roleId = role.id
				// console.log(this.limitsInfo)
				this.getLeafKeys(role,this.defKeys)
				this.dialogVisible = true
				console.log(role)
			},
			// ,
			getLeafKeys(node,arr){
				// nodechildren
				if(!node.children){
					// iddefKeys
					return arr.push(node.id)
				}
				node.children.forEach((item)=>{
					this.getLeafKeys(item,arr)
				})
			},
			// 
			setRightDialogClosed(){
				this.defKeys = []
			},
			// api
			async allotRights(){
				// ...keys[[150],[101,104]],...keys[150,101,104]
				const keys = [
					...this.$refs.treeRef.getCheckedKeys(),
					...this.$refs.treeRef.getHalfCheckedKeys(),
					
				]
				// join() ,idStr101102  ,idStr101,102
				const idStr = keys.join(",")
				// api
				const {data:res} = await this.$http.post(`roles/${this.roleId}/rights`,{rids:idStr})
				if(res.meta.status !==200){
					return this.$message.error("")
				}
				this.$message.success("")
				//  
				this.getroleInfoList()
				// console.log(res)
				this.dialogVisible = false
				// console.log(idStr) 
			}
		}
	}
</script>

<style>
	.el-tag {
		margin: 10px;
	}

	.bdtop {
		border-top: 1px solid #eee;
	}

	.bdbottom {
		border-bottom: 1px solid #eee;
	}
</style>
