<template>
	<div>
		<!--  -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/index' }"></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>
		<!--  -->
		<el-card class="box-card">
			<!-- element-uilayout -->
			<!--  -->
			<el-row :gutter="20">
				<!--  -->
				<el-col :span="7">
					<!--  -->
					<el-input placeholder="" v-model="queryList.query" clearable @change="getUserList">
						<el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
					</el-input>
				</el-col>
				<el-col :span="4">
					<el-button type="primary" @click="dialogVisible = true"></el-button>
				</el-col>
			</el-row>
			<!--  -->
			<el-table :data="userList" border stripe>
				<el-table-column type="index" label="#"></el-table-column>
				<el-table-column prop="username" label=""></el-table-column>
				<el-table-column prop="email" label=""></el-table-column>
				<el-table-column prop="mobile" label=""></el-table-column>
				<el-table-column prop="role_name" label=""></el-table-column>
				<el-table-column prop="mg_state" label="">
					<template slot-scope="scope">
						<!-- scope -->
						<el-switch v-model="scope.row.mg_state" @change="changeState(scope.row)">
						</el-switch>
					</template>
				</el-table-column>
				<el-table-column prop="mobile" label="">
					<template slot-scope="scope">
						<el-button type="primary" icon="el-icon-edit" size="small" @click="changeUserInfo(scope.row.id)"></el-button>
						<el-button type="danger" icon="el-icon-delete" size="small" @click="removeUserInfo(scope.row.id)"></el-button>
						<el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
							<el-button type="warning" icon="el-icon-setting" size="small" @click="setRole(scope.row)"></el-button>
						</el-tooltip>
					</template>
				</el-table-column>
			</el-table>
			<!--  -->
			<div class="block">
				<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-sizes="[1, 2, 4, 10]"
				 :page-size="queryList.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
				</el-pagination>
			</div>
		</el-card>
		<!--  -->
		<el-dialog title="" :visible.sync="dialogVisible" width="50%" :before-close="handleClose" @close="addDialogClosed">
			<!--  -->
			<el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
				<el-form-item label="" prop="username">
					<el-input v-model="addForm.username"></el-input>
				</el-form-item>
				<el-form-item label="" prop="password">
					<el-input v-model="addForm.password"></el-input>
				</el-form-item>
				<el-form-item label="" prop="email">
					<el-input v-model="addForm.email"></el-input>
				</el-form-item>
				<el-form-item label="" prop="mobile">
					<el-input v-model="addForm.mobile"></el-input>
				</el-form-item>
			</el-form>
			<span slot="footer" class="dialog-footer">
				<el-button @click="dialogVisible = false"> </el-button>
				<el-button @click="addUser" type="primary"> </el-button>
			</span>
		</el-dialog>
		<!--  -->
		<el-dialog title="" :visible.sync="editDialogVisible" width="50%" :before-close="handleClose" @close="editDialogClosed">
			<el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
				<el-form-item label="">
					<el-input v-model="editForm.username" disabled></el-input>
				</el-form-item>
				<el-form-item label="" prop="email">
					<el-input v-model="editForm.email"></el-input>
				</el-form-item>
				<el-form-item label="" prop="mobile">
					<el-input v-model="editForm.mobile"></el-input>
				</el-form-item>
			</el-form>
			<span slot="footer" class="dialog-footer">
				<el-button @click="editDialogVisible = false"> </el-button>
				<el-button type="primary" @click="editUserInfo"> </el-button>
			</span>
		</el-dialog>
		<!--  -->
		<el-dialog title="" :visible.sync="setRoleDialogVisible" width="50%">
			<div>
				<p>{{userInfo.username}}</p>
				<p>{{userInfo.role_name}}</p>
				<p><el-select v-model="selectedRoleId" placeholder="">
						<el-option v-for="item in rolesList" :key="item.id" :label="item.roleName" :value="item.id">
						</el-option> 
					</el-select>
				</p>
			</div>
			<span slot="footer" class="dialog-footer">
				<el-button @click="setRoleDialogVisible = false"> </el-button>
				<el-button type="primary" @click="putSetRole"> </el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	export default {
		data() {
			//
			var checkEmail = (rule, value, cb) => {
				//
				const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
				if (regEmail.test(value)) {
					//
					return cb()
				}
				cb(new Error(''))
			};
			//
			var checkMobile = (rule, value, cb) => {
				//
				const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
				if (regMobile.test(value)) {
					//
					return cb()
				}
				cb(new Error(''))
			};
			return {

				value: true,
				queryList: {
					query: '',
					pagenum: 1,
					pagesize: 4,
				},
				dialogVisible: false,
				editDialogVisible: false,
				setRoleDialogVisible: false,
				userList: [],
				//
				total: 0,
				//
				addForm: {
					username: '',
					password: '',
					email: '',
					mobile: '',
				},
				editForm: {},
				editFormRules: {
					email: [{
							required: true,
							message: '',
							trigger: 'blur'
						},
						{
							validator: checkEmail,
							trigger: 'blur'
						}
					],
					mobile: [{
							required: true,
							message: '',
							trigger: 'blur'
						},
						{
							validator: checkMobile,
							trigger: 'blur'
						}
					],
				},
				addFormRules: {
					username: [{
							required: true,
							message: '',
							trigger: 'blur'
						},
						{
							min: 3,
							max: 10,
							message: ' 3  10 ',
							trigger: 'blur'
						}
					],
					password: [{
							required: true,
							message: '',
							trigger: 'blur'
						},
						{
							min: 3,
							max: 10,
							message: ' 3  10 ',
							trigger: 'blur'
						}
					],
					email: [{
							required: true,
							message: '',
							trigger: 'blur'
						},
						{
							validator: checkEmail,
							trigger: 'blur'
						}
					],
					mobile: [{
							required: true,
							message: '',
							trigger: 'blur'
						},
						{
							validator: checkMobile,
							trigger: 'blur'
						}
					],
				},
				// 
				userInfo: {},
				// 
				rolesList:[],
				selectedRoleId:''
			}
		},
		created() {
			this.getUserList()
		},
		methods: {
			//
			handleClose() {
				this.dialogVisible = false
				this.editDialogVisible = false
			},
			//
			handleSizeChange(val) {
				// console.log(` ${val} `);
				this.queryList.pagesize = val
				this.getUserList()
			},
			//
			handleCurrentChange(val) {
				// console.log(`: ${val}`);
				this.queryList.pagenum = val
				this.getUserList()
			},
			//switch
			async changeState(rowinfo) {
				// console.log(rowinfo)
				const {
					data: res
				} = await this.$http.put(`users/${rowinfo.id}/state/${rowinfo.mg_state}`)
				// console.log(res)
				if (res.meta.status !== 200) {
					//
					rowinfo.mg_state = !rowinfo.mg_state
					return this.$message.error("")
				}
				this.$message.success("")
			},
			async getUserList() {
				const {
					data: res
				} = await this.$http.get('users', {
					params: this.queryList
				})
				// console.log(res)
				if (res.meta.status != 200) return this.$message.error('')
				this.userList = res.data.users
				// console.log(this.userList)
				this.total = res.data.total
				// console.log(this.userList)
			},
			//
			addDialogClosed() {
				this.$refs.addFormRef.resetFields()
			},
			//
			editDialogClosed() {
				this.$refs.editFormRef.resetFields()
			},
			//npm 
			addUser() {
				this.$refs.addFormRef.validate(async v => {
					//
					if (!v) return
					//
					const {
						data: res
					} = await this.$http.post("users", this.addForm)
					// console.log(res)
					//
					if (res.meta.status !== 201) {
						this.$message.error("")
					}
					this.$message.success("")
					this.getUserList()
					this.dialogVisible = false
				})
			},
			//
			editUserInfo() {
				const tse = {
					email: this.editForm.email,
					mobile: this.editForm.mobile
				}
				this.$refs.editFormRef.validate(async v => {
					//
					if (!v) return
					const {
						data: res
					} = await this.$http.put(`users/${this.editForm.id}`, tse)
					//
					// this.$http.put('users' + this.editForm.id,{email:this.editForm.email,
					// mobile:this.editForm.mobile
					// })
					if (res.meta.status !== 200) {
						return this.$message.error("")
					}
					this.$message.success("")
					console.log(res)
				})
				this.editDialogVisible = false
				this.getUserList()
			},
			//
			async changeUserInfo(id) {
				// console.log(userinfo)
				//scopeid,id
				const {
					data: res
				} = await this.$http.get(`users/${id}`)
				console.log(res)
				if (res.meta.status !== 200) {
					return this.$message.error("")
				}
				this.editForm = res.data
				this.editDialogVisible = true
			},
			//id 
			removeUserInfo(id) {
				//
				this.$confirm(', ?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(async () => {
					const {
						data: res
					} = await this.$http.delete(`users/${id}`)
					this.getUserList()
					console.log(res)
					this.$message({
						type: 'success',
						message: '!'
					});
				}).catch(() => {
					this.$message({
						type: 'info',
						message: ''
					});
				});
			},
			// 
			async setRole(userInfo) {
				this.userInfo = userInfo
				// 
				const {
					data: res
				} = await this.$http.get("roles")
				if (res.meta.status !== 200) {
					return this.$message.error("")
				}
				this.rolesList = res.data
				// console.log(this.rolesList)
				this.setRoleDialogVisible = true
				// console.log(userInfo)
			},
			// 
			async putSetRole(){
				if(!this.selectedRoleId){
					return this.$message.error("")
				}
				const {data:res} = await this.$http.put(`users/${this.userInfo.id}/role`,{rid:this.selectedRoleId})
				if(res.meta.status !== 200){
					return this.$message.error("")
				}
				this.$message.success("")
				this.getUserList()
				this.setRoleDialogVisible = false
				// console.log(res)
				// console.log(this.selectedRoleId)
			}
				
		},

	}
</script>

<style>
	.el-card {
		margin-top: 20px;
	}

	.el-table {
		margin-top: 15px;
	}

	.block {
		margin-top: 15px;
	}
	p{
		font-size: 12px;
		margin: 10px;
	}
</style>
