<template>
	<div>
		<!--  -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/index' }"></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>
		<!--   -->
		<el-card>
			<!--  -->
			<el-row :gutter="20">
				<!--  -->
				<el-col :span="7">
					<!--  -->
					<el-input placeholder="" clearable>
						<el-button slot="append" icon="el-icon-search"></el-button>
					</el-input>
				</el-col>
			</el-row>
			<!--  -->
			<!--  -->
			<el-table :data="orderList" style="width: 100%" border>
				<el-table-column type="index" label="#"></el-table-column>
				<el-table-column prop="order_number" label="">
				</el-table-column>
				<el-table-column prop="order_price" label="" width="80">
				</el-table-column>
				<el-table-column prop="pay_status" label="" width="95">
					<template slot-scope="scope">
						<el-tag type="danger">{{scope.row.pay_status}}</el-tag>
					</template>
				</el-table-column>
				<el-table-column prop="is_send" label="" width="75"></el-table-column>
				<el-table-column prop="create_time" label="" width="180">
					<template slot-scope="scope">
						{{scope.row.create_time | dateFormat}}
					</template>
				</el-table-column>
				<el-table-column label="" width="150">
					<el-button type="primary" icon="el-icon-edit" size="mini" @click="showDialog"></el-button>
					<el-button type="success" icon="el-icon-location-outline" size="mini" @click="showProcessDialog"></el-button>
				</el-table-column>
			</el-table>
			<!--  -->
			<!--  -->
			<div class="block">
				<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum"
				 :page-sizes="[5, 7, 10,15]" :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper"
				 :total="total">
				</el-pagination>
			</div>
			<!--  -->
			<!--  -->
			<el-dialog title="" :visible.sync="dialogVisible" width="50%" @close="handleClose">
				<span slot="footer" class="dialog-footer">
					<!--  -->
					<el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef" label-width="100px">
						<el-form-item label="/" prop="address1">
							<el-cascader expand-trigger="hover" v-model="addressForm.address1" :options="cityData"></el-cascader>
						</el-form-item>
						<el-form-item label="" prop="address2">
							<el-input v-model="addressForm.address2"></el-input>
						</el-form-item>
					</el-form>
					<el-button @click="dialogVisible = false"> </el-button>
					<el-button type="primary" @click="dialogVisible = false"> </el-button>
				</span>
			</el-dialog>
			<!--  -->
			<el-dialog title="" :visible.sync="process_dialogVisible" width="50%">
				<el-timeline>
				    <el-timeline-item
				      v-for="(item, index) in processInfo.data"
				      :key="index"
				      :timestamp="item.time">
				      {{item.context}}
				    </el-timeline-item>
				  </el-timeline>
				<span slot="footer" class="dialog-footer">
					<el-button @click="process_dialogVisible = false"> </el-button>
					<el-button type="primary" @click="process_dialogVisible = false"> </el-button>
				</span>
			</el-dialog>

		</el-card>
	</div>
</template>

<script>
	import cityData from "./citydata.js"
	export default {
		data() {
			return {
				queryInfo: {
					pagenum: 1,
					pagesize: 5
				},
				orderList: [],
				total: 0,
				dialogVisible: false,
				addressForm: {
					address1: [],
					address2: ''
				},
				addressFormRules: {
					address1: [{
						required: true,
						message: '/',
						trigger: 'blur'
					}],
					address2: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
				},
				cityData,
				process_dialogVisible: false,
				processInfo: {
					data: [{
							time: "2018-05-10 09:39:00",
							ftime: "2018-05-10 09:39:00",
							context: ",,",
							location: ""
						},
						{
							time: "2018-05-10 08:23:00",
							ftime: "2018-05-10 08:23:00",
							context: "[]  95338",
							location: ""
						},
						{
							time: "2018-05-10 07:32:00",
							ftime: "2018-05-10 07:32:00",
							context: " []",
							location: ""
						},
						{
							time: "2018-05-10 02:03:00",
							ftime: "2018-05-10 02:03:00",
							context: "[], []",
							location: ""
						},
						{
							time: "2018-05-09 23:05:00",
							ftime: "2018-05-09 23:05:00",
							context: " []",
							location: ""
						},
						{
							time: "2018-05-09 21:21:00",
							ftime: "2018-05-09 21:21:00",
							context: "[], []",
							location: ""
						},
						{
							time: "2018-05-09 13:07:00",
							ftime: "2018-05-09 13:07:00",
							context: " ",
							location: ""
						},
						{
							time: "2018-05-09 12:25:03",
							ftime: "2018-05-09 12:25:03",
							context: "",
							location: ""
						},
						{
							time: "2018-05-09 12:22:24",
							ftime: "2018-05-09 12:22:24",
							context: "HLA",
							location: ""
						},
						{
							time: "2018-05-08 21:36:04",
							ftime: "2018-05-08 21:36:04",
							context: "",
							location: ""
						}
					]
				}
			}
		},
		created() {
			this.getOrderListInfo()
		},
		computed: {},
		methods: {
			// 
			async getOrderListInfo() {
				const {
					data: res
				} = await this.$http.get("orders", {
					params: this.queryInfo
				})
				if (res.meta.status !== 200) {
					return this.$message.error("")
				}
				this.orderList = res.data.goods
				this.orderList.forEach((v, i) => {
					if (v.pay_status === "0") {
						v.pay_status = ""
					} else {
						v.pay_status = ""
					}
				})
				this.total = res.data.total
				console.log(this.orderList)
			},
			// 
			handleSizeChange(options) {
				this.queryInfo.pagesize = options
				this.getOrderListInfo()
			},
			handleCurrentChange(options) {
				this.queryInfo.pagenum = options
				this.getOrderListInfo()
			},
			// 
			showDialog() {
				this.dialogVisible = true
			},
			// 
			handleClose() {
				this.dialogVisible = false
				this.$refs.addressFormRef.resetFields()
			},
			// ,
			async showProcessDialog() {
				this.process_dialogVisible = true
				console.log(this.processInfo)
			}

		}
	}
</script>

<style scoped>
	.el-table {
		font-size: 12px;
	}
	.el-cascader {
		width: 100%;
	}
	.el-timeline{
		font-size: 14px;
	}
</style>
