<template>
	<div class="container">
		<div class="login-moudle">
			<div class="login-img">
				<img src="../assets/logo.png" />
			</div>
			<div class="demo-input-suffix">
				<el-form :rules="rules" ref="ruleForm" :model="ruleForm">
					<el-form-item prop="username">
						<el-input placeholder="" prefix-icon="el-icon-user" class="input1" v-model="ruleForm.username">
						</el-input>
					</el-form-item>
					<el-form-item prop="password">
						<el-input placeholder="" prefix-icon="el-icon-lock" v-model="ruleForm.password" type="password">
						</el-input>
					</el-form-item>
				</el-form>
			</div>
			<el-row class="btn">
					<el-button type="primary" @click="login"></el-button>
					<el-button type="success" @click="resetForm"></el-button>
			</el-row>
		</div>
	</div>

</template>

<script>
	export default {
		data() {
			return {
				ruleForm: {
					username: 'admin',
					password: '123456',
				},
				rules: {
					username: [{
							required: true,
							message: '',
							trigger: 'blur'
						},
						{ 
							min: 4,
							max: 10,
							message: ' 6  10 ',
							trigger: 'blur'
						}
					],
					password: [{
							required: true,
							message: '',
							trigger: 'blur'
						},
						{
							min: 3,
							max: 10,
							message: '6',
							trigger: 'blur'
						}
					],
				}
			}
		},
		methods: {
			//
			resetForm(){
				// console.log(this)
				this.$refs.ruleForm.resetFields()
			},
			//
			login(){
				this.$refs.ruleForm.validate(async (vaild)=>{
					if(!vaild) return 
					// console.log(vaild)
					//axiospst
					// async awaitpromise
					//datares
					const {data:res} = await this.$http.post("login",this.ruleForm)
					// console.log(res)
					//
					// const res= await this.$http.post("login",this.ruleForm)
					// console.log(res.data)
					if(res.meta.status!=200) return this.$message.error("")
					this.$message.success("")
					/*
						1.token sessionStorage
							1.1api
							1.2 tokentokensessionStorage
						2./index
					*/
				   window.sessionStorage.setItem("token",res.data.token)
				   this.$router.push("/index")
				})
			}
		}
	} 
</script>

<style scoped>
	.el-button--primary {
		margin-right: 10px;
	}

	a {
		text-decoration: none;
	}

	.container {
		width: 100%;
		height: 100%;
		background-color: #0085A1;
	}

	.login-moudle {
		width: 500px;
		height: 350px;
		background-color: white;
		position: absolute;
		display: flex;
		flex-direction: column;
		align-items: center;
		top: 50%;
		left: 50%;
		transform: translate(-250px, -175px);
	}
	.login-img {
		width: 122px;
		height: 122px;
		border: 2px solid #ffffff;
		border-radius: 50%;
		position: absolute;
		top: -61px;
		left: 173px;
		z-index: 99;
		box-shadow: 0 0 8px;
	}
	.login-moudle img {
		width: 120px;
		height: 120px;
		border: 1px solid #ffffff;
		background-color: #e5e5e5;
		border-radius: 50%;
	}

	.demo-input-suffix {
		margin-top: 100px;
		width: 70%;
	}

	.input1 {
		margin-bottom: 30px;
	}
	.btn {
		margin-top: 40px;
		margin-left: 200px;
	}
</style>
