<template>
	<div>
		<!--  -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/index' }"></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>
		<!--   -->
		<!--  -->
		<el-card class="box-card">
			<!--  -->
			<el-alert show-icon title=":  " type="warning">
			</el-alert>
			<div class="chooseCategory">
				<span>
					<!--  -->
					<!-- options  -->
					<!-- props  -->
					<el-cascader expand-trigger="hover" :options="cateList" :props="cascaderProps" v-model="selectedKeys" @change="parentCateChanged"
					 clearable></el-cascader>
				</span>
				<!-- tabs -->
				<el-tabs v-model="activeName" @tab-click="handleTabClick">
					<!--  -->
					<el-tab-pane label="" name="many">
						<el-button type="primary" :disabled="isBtnDisabled" @click="dialogVisible = true"></el-button>
						<!--  -->
						<el-table border :data="manyTableData">
							<el-table-column type="expand">
								<template slot-scope="scope">
									<el-tag closable v-for="(item,index) in scope.row.attr_vals" :key="index" @close="handleClose(scope.row,index)">{{item}}</el-tag>
									<el-input class="input-new-tag" v-if="scope.row.inputVisible" v-model="scope.row.inputValue" ref="saveTagInput"
									 size="small" @keyup.enter.native="handleInputConfirm(scope.row)" @blur="handleInputConfirm(scope.row)">
									</el-input>
									<el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>

								</template>
							</el-table-column>
							<el-table-column type="index" label="#"></el-table-column>
							<el-table-column prop="attr_name" label=""></el-table-column>
							<el-table-column prop="email" label="">
								<template slot-scope="scope">
									<el-button type="primary" icon="el-icon-edit" size="mini" @click="showDialog(scope.row.attr_id)"></el-button>
									<el-button type="danger" icon="el-icon-delete" size="mini"></el-button>
								</template>
							</el-table-column>
						</el-table>
						<!--  -->
					</el-tab-pane>
					<!--  -->
					<el-tab-pane label="" name="only">
						<el-button type="primary" :disabled="isBtnDisabled" @click="dialogVisible = true"></el-button>
						<!--  -->
						<el-table border :data="onlyTableData">
							<el-table-column type="expand">
								<template slot-scope="scope">
									<el-tag closable v-for="(item,index) in scope.row.attr_vals" :key="index" @close="handleClose(scope.row,index)">{{item}}</el-tag>
									<el-input class="input-new-tag" v-if="scope.row.inputVisible" v-model="scope.row.inputValue" ref="saveTagInput"
									 size="small" @keyup.enter.native="handleInputConfirm(scope.row)" @blur="handleInputConfirm(scope.row)">
									</el-input>
									<el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
								</template>
							</el-table-column>
							<el-table-column type="index" label="#"></el-table-column>
							<el-table-column prop="attr_name" label=""></el-table-column>
							<el-table-column prop="email" label="">
								<template slot-scope="scope">
									<el-button type="primary" icon="el-icon-edit" size="mini" @click="showDialog(scope.row.attr_id)"></el-button>
									<el-button type="danger" icon="el-icon-delete" size="mini"></el-button>
								</template>
							</el-table-column>
						</el-table>
						<!--  -->
					</el-tab-pane>
				</el-tabs>
			</div>
			<!--  -->
			<el-dialog :title="'' + titleText" :visible.sync="dialogVisible" width="50%" @close="addDiolagClosed">
				<el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
					<el-form-item :label="titleText" prop="attr_name">
						<el-input v-model="addForm.attr_name"></el-input>
					</el-form-item>
				</el-form>
				<span slot="footer" class="dialog-footer">
					<el-button @click="dialogVisible = false"> </el-button>
					<el-button type="primary" @click="addParams"> </el-button>
				</span>
			</el-dialog>
			<!--  -->
			<el-dialog :title="'' + titleText" :visible.sync="editDialogVisible" width="50%" @close="editiolagClosed">
				<el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
					<el-form-item :label="titleText" prop="attr_name">
						<el-input v-model="editForm.attr_name"></el-input>
					</el-form-item>
				</el-form>
				<span slot="footer" class="dialog-footer">
					<el-button @click="editDialogVisible = false"> </el-button>
					<el-button type="primary" @click="editParams"> </el-button>
				</span>
			</el-dialog>
		</el-card>
		<!--  -->
	</div>
</template>

<script>
	export default {
		data() {
			return {
				// 
				cateList: [],
				// // 
				// queryInfo: {
				// 	type: 3,
				// 	pagenum: 1,
				// 	pagesize: 5
				// },
				cascaderProps: {
					value: 'cat_id',
					label: 'cat_name',
					children: 'children'
				},
				// 
				selectedKeys: [],
				activeName: 'many',
				manyTableData: [],
				onlyTableData: [],
				dialogVisible: false,
				editDialogVisible: false,
				addForm: {
					attr_name: '',
					attr_sel: '',
					attr_vals: ''
				},
				editForm: {
					attr_name: '',
					attr_sel: '',
					attr_vals: ''
				},
				// 
				editFormRules: {
					attr_name: [{
							required: true,
							message: '',
							trigger: 'blur'
						},
						{
							min: 2,
							max: 10,
							message: ' 2  10 ',
							trigger: 'blur'
						}
					],
				},
				// 
				addFormRules: {
					attr_name: [{
							required: true,
							message: '',
							trigger: 'blur'
						},
						{
							min: 2,
							max: 10,
							message: ' 2  10 ',
							trigger: 'blur'
						}
					],
				},
				inputVisible: false,
				inputValue: ''
			}
		},
		created() {
			this.getCateList()
		},
		computed: {
			isBtnDisabled() {
				if (this.selectedKeys.length !== 3) {
					return true
				}
				return false
			},
			// id
			catId() {
				if (this.selectedKeys.length === 3) {
					return this.selectedKeys[2]
				}
				return null
			},
			titleText() {
				if (this.activeName === "many") {
					return ""
				}
				return ""
			},
		},
		methods: {
			// 
			async getCateList() {
				const {
					data: res
				} = await this.$http.get("categories")
				if (res.meta.status !== 200) {
					return this.$message.error("")
				}
				// cateList
				this.cateList = res.data
				// console.log(this.cateList)
			},
			// 
			parentCateChanged() {
				// selectedKeys
				// console.log(this.selectedKeys)
				// selectedKeys
				if(this.selectedKeys.length !== 3){
					this.selectedKeys = []
					this.manyTableData = []
					this.onlyTableData = []
					return
				}
				this.getParamsData()
			},
			async getParamsData() {
				// console.log(this.selectedKeys)
				// selectedKeys3
				if (this.selectedKeys.length !== 3) {
					this.selectedKeys = []
					return
				}
				//  
				const {
					data: res
				} = await this.$http.get(`categories/${this.catId}/attributes`, {
					params: {
						sel: this.activeName
					}
				})
				if (res.meta.status !== 200) {
					return this.$message.error("")
				}
				// console.log(res.data)
				res.data.forEach(v => {
					v.attr_vals = v.attr_vals ? v.attr_vals.split(" ") : [],
						v.inputVisible = false
					v.inputValue = ''
				})
				// 
				if (this.activeName === "many") {
					this.manyTableData = res.data

				} else {
					this.onlyTableData = res.data
				}
				// console.log(this.manyTableData)
				// console.log(this.onlyTableData)
				// 
			},
			// tab
			handleTabClick() {
				// console.log(this.activeName)
				this.getParamsData()
			},
			// 
			addDiolagClosed() {
				this.$refs.addFormRef.resetFields()
			},
			editiolagClosed() {
				this.$refs.editFormRef.resetFields()
			},
			// 
			addParams() {
				// 
				this.$refs.addFormRef.validate(async valid => {
					if (!valid) return
					const {
						data: res
					} = await this.$http.post(`categories/${this.catId}/attributes`, {
						attr_name: this.addForm.attr_name,
						attr_sel: this.activeName
					})
					if (res.meta.status !== 201) {
						return this.$message.error("")
					}
					this.$message.success("")
					this.dialogVisible = false
					this.getParamsData()
					// console.log(res)
				})

			},
			// 
			async showDialog(attrId) {
				// id
				const {
					data: res
				} = await this.$http.get(`categories/${this.catId}/attributes/${attrId}`, {
					attr_sel: this.activeName
				})
				this.editForm = res.data
				console.log(this.editForm)
				this.editDialogVisible = true
			},
			// 
			editParams() {
				this.$refs.editFormRef.validate(async valid => {
					if (!valid) return
					const {
						data: res
					} = await this.$http.put(`categories/${this.catId}/attributes/${this.editForm.attr_id}`, {
						attr_name: this.editForm.attr_name,
						attr_sel: this.activeName
					})
					if (res.meta.status !== 200) {
						return this.$message.error("")
					}
					this.$message.success("")

					this.getParamsData()
					this.editDialogVisible = false
					console.log(res.data)
				})

			},
			// enter
			async handleInputConfirm(row) {
				// 
				if(row.inputValue.trim().length === 0){
					row.inputValue = ''
					row.inputVisible = false
					return
				}
				// return
				row.attr_vals.push(row.inputValue.trim())
				row.inputValue = ''
				row.inputVisible = false
				// ,
				console.log(row)
				const {data:res} = await this.$http.put(`categories/${row.cat_id}/attributes/${row.attr_id}`,{
					attr_name:row.attr_name,
					attr_sel:row.attr_sel,
					attr_vals:row.attr_vals.join(" ")
				})
				// joinattrvals
					// console.log(res.data)
				if(res.meta.status !== 200){
					return this.$message.error("")
				}
				this.$message.success("")
			},
			showInput(row) {
				row.inputVisible = true
				// 
				// $nextTick
				this.$nextTick(_ => {
					this.$refs.saveTagInput.$refs.input.focus();
				});
			},
			// 
			async handleClose(row,index){
				row.attr_vals.splice(index,1)
				const {data:res} = await this.$http.put(`categories/${row.cat_id}/attributes/${row.attr_id}`,{
					attr_name:row.attr_name,
					attr_sel:row.attr_sel,
					attr_vals:row.attr_vals.join(' ')
				})
				
				// joinattrvals
					console.log(res.data)
				if(res.meta.status !== 200){
					return this.$message.error("")
				}
				this.$message.success("")
				console.log(row,index)
			}
		}
	}
</script>

<style>
	.chooseCategory {
		margin-top: 20px;
		font-size: 14px;
	}

	.el-cascader {
		margin-bottom: 20px;
	}
</style>
