<template>
	<div>
		<!--  -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/index' }"></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>
		<!--   -->
		<el-card>
			<!--  -->
			<el-row :gutter="20">
				<!--  -->
				<el-col :span="7">
					<!--  -->
					<el-input placeholder=""  clearable v-model="queryInfo.query" @clear="getGoodsList">
						<el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
					</el-input>
				</el-col>
				<el-col :span="4">
					<el-button type="primary" @click="toAddGoodsPage"></el-button>
				</el-col>
			</el-row>
			<!--  -->
			<el-table :data="goodsList" border stripe>
				<el-table-column type="index" label="#"></el-table-column>
				<el-table-column   prop="goods_name" label=""></el-table-column>
				<el-table-column prop="goods_price" label="()" width="95px"></el-table-column>
				<el-table-column prop="goods_weight" label="" width="75px"></el-table-column>
				<el-table-column prop="add_time" label="" width="140px">
					<template slot-scope="scope">
						{{scope.row.add_time | dateFormat}}
					</template>
				</el-table-column>
				<el-table-column prop="goods_state" label="" width="150px">
					<template slot-scope="scope">
						<el-button type="primary" icon="el-icon-edit" size="small" ></el-button>
						<el-button type="danger" icon="el-icon-delete" size="small" @click="deleteGoodsById(scope.row)" ></el-button>
					</template>
				</el-table-column>
			</el-table>
			<!--  -->
			<!--  -->
			<div class="block">
			    <el-pagination
			      @size-change="handleSizeChange"
			      @current-change="handleCurrentChange"
			      :page-sizes="[5,7,9,12]"
			      :page-size="queryInfo.pagesize"
			      layout="total, sizes, prev, pager, next, jumper"
			      :total="total"
				  background
				  >
			    </el-pagination>
			  </div>
		</el-card>
		
	</div>
</template>

<script>
	export default{
		data(){
			return{
				queryInfo:{
					query:'',
					pagenum:1,
					pagesize:5
				},
				total:0,
				goodsList:[]
			}
		},
		created() {
			this.getGoodsList()
		},
		methods:{
			// 
			 async getGoodsList(){
				const {data:res} = await this.$http.get("goods",{params:this.queryInfo})
				if(res.meta.status !== 200){
					return this.$message.error("")
				}
				this.goodsList = res.data.goods
				this.total = res.data.total
				console.log(res)
			},
			// 
			handleSizeChange(options){
				this.queryInfo.pagesize = options
				this.getGoodsList()
				// console.log(options)
			},
			// 
			handleCurrentChange(options){
				this.queryInfo.pagenum = options
				this.getGoodsList()
				console.log(options)
			},
			// id
			async deleteGoodsById(options){
				const {data:res} = await this.$http.delete(`goods/${options.goods_id}`)
				if(res.meta.status !== 200){
					return this.$message.error("")
				}
				this.$message.success("")
				this.getGoodsList()
				console.log(options)
			},
			// 
			toAddGoodsPage(){
				this.$router.push('/goods/add')
			}
		}
	}
</script> 

<style scoped>
	.el-table{
		font-size: 12px;
	}
</style>
